<template>
  <div>
     <div class="py-4 px-2 shadow">
        <p class="mt-1 font-semibold text-lg float-right mr-3">QR Scanner</p>
        <button @click="$router.go(-1)" type="button"  class="btn btn-sm btn-dark-outline ml-2 rounded-circle px-2" style="border:1px solid black;">
            <span class="typcn typcn-arrow-left"></span>
        </button>
    </div>
    <div class="row">
      <br>
      <br>
      <br>
      <div id="reader" style="height:350px;width:100vw;" class="mt-12"></div>
        <!-- <div class="col-sm-12" v-if="!streamOn" style="height:100vh;" @click="getStream('video');streamOn=true;$forceUpdate()">
          <div style="height:90vh;" class="d-flex justify-content-center align-items-center shadow-lg rounded-lg">
              <div style="width:100%">
                  <p class="text-center text-4xl font-semibold">
                    Klik disini untuk Start Camera!
                  </p>
              </div>
          </div>
        </div>
        <div v-else class="col-sm-12">
          <video autoplay playsInline muted style="height:60vh; width: 100vh;" id="videoQRCODE"></video>
          <div class="row justify-content-center mt-2">
            <div class="col-sm-6">
              <button type="button" class="btn btn-sm btn-style8 text-sm p-3 btn-block" @click="scanQR" v-if="loading">Scan QR!</button>
            </div>
          </div>
        </div> -->
    </div>
  </div>
</template>
<script>
import QrScanner from "qr-scanner";
import {Html5QrcodeScanner} from "html5-qrcode"
import {Html5Qrcode} from "html5-qrcode"
import axios from "axios";
import autophp from "@/plugins/auto";
let sdb = new autophp("ci");
export default {
  data() {
    return {
      loading: false,
      isIos: false,
      countIos: 0,
      streamOn:false
    };
  },
  methods: {
    scanQR() {
      let that = this;
      let videoElem = document.getElementById("videoQRCODE");
      QrScanner.scanImage(videoElem)
        .then((result) => {
          console.log("result", result);
          result=JSON.parse(result);
          sdb.alert("QR Code!",'bg-green-400');
          that.$router.push(`/absen/gps?type=${this.$route.query.type}&target=${this.$route.query.target}&latitude=${result.lat}&longitude=${result.long}`);
        })
        .catch((error) => {
          that.$router.push(`/absen/gps?type=${this.$route.query.type}&target=${this.$route.query.target}&latitude=${this.$route.query.latitude}&longitude=${this.$route.query.longitude}`);
          sdb.alert("QR Code!");
          console.log(error || "No QR code found.");
        });
    },
     getStream(type) {
      if (
        !navigator.mediaDevices &&
        !navigator.getUserMedia &&
        !navigator.webkitGetUserMedia &&
        !navigator.mozGetUserMedia &&
        !navigator.msGetUserMedia
      ) {
        alert("User Media API not supported.");
        return;
      }
      setTimeout(() => {
        this.loading=true;
        this.$forceUpdate();
      }, 2000);
      var constraints = {};
      constraints[type] = true;

      this.getUserMedia(constraints)
        .then(function (stream) {
          var mediaControl = document.querySelector(type);

          if ("srcObject" in mediaControl) {
            mediaControl.srcObject = stream;
          } else if (navigator.mozGetUserMedia) {
            mediaControl.mozSrcObject = stream;
          } else {
            mediaControl.src = (window.URL || window.webkitURL).createObjectURL(
              stream
            );
          }

          mediaControl.play();
        })
        .catch(function (err) {
          alert("Error: " + err);
        });
    },
    getUserMedia(constraints) {
      // if Promise-based API is available, use it
      if (navigator.mediaDevices) {
        setTimeout(() => {
          this.checkCamera();
        }, 1500);
        return navigator.mediaDevices.getUserMedia({
          video:{
            facingMode: "environment", //'environment':'user',
          }
        });
      }
      // otherwise try falling back to old, possibly prefixed API...
      var legacyApi =
        navigator.getUserMedia ||
        navigator.webkitGetUserMedia ||
        navigator.mozGetUserMedia ||
        navigator.msGetUserMedia;

      if (legacyApi) {
        // ...and promisify it
        return new Promise(function (resolve, reject) {
          legacyApi.bind(navigator)(constraints, resolve, reject);
        });
      }
    },
    checkCamera(){
      Html5Qrcode.getCameras().then(devices => {
        /**
         * devices would be an array of objects of type:
         * { id: "id", label: "label" }
         */
        console.log('devices',devices)
        if (devices && devices.length) {
          var cameraId = devices[0].id;
          // .. use this to start scanning.
        }
      }).catch(err => {
        // handle err
      });
    },
    startScanner(){
      let that=this;
      const html5QrCode = new Html5Qrcode("reader");
      const qrCodeSuccessCallback = (decodedText, decodedResult) => {
        let result = JSON.parse(decodedText);
        that.$router.push(`/absen/gps?type=${this.$route.query.type}&target=${this.$route.query.target}&latitude=${result.lat}&longitude=${result.long}`);
        html5QrCode.stop().then((ignore) => {
          // QR Code scanning is stopped.
        }).catch((err) => {
          // Stop failed, handle it.
        });
      };
      const config = { fps: 10, qrbox: { width: 250, height: 250 } };

      // If you want to prefer back camera
      html5QrCode.start({ facingMode: "environment" }, config, qrCodeSuccessCallback);

      // html5QrCode.start({ facingMode: { exact: "environment"} }, config, qrCodeSuccessCallback);
    
    }
  },
  mounted() {
    let that = this;
    // this.startVideo();

    this.startScanner();
    
    if (localStorage.getItem("users")) {
    } else {
      this.$router.push("/");
    }
  },
};
</script>